import {
  COLLAPSE, 
  DIALOG_SHOW_GRAPH
}from './mutation-type'

export default {
  //侧边栏
  ['COLLAPSE'](state) {
    state.collapse = !state.collapse
  },
  ['DIALOG_SHOW_GRAPH'](state){
    state.dialogshowGraph = !state.dialogshowGraph;
  },
}



