import Vue from 'vue'
import Router from 'vue-router'

const Login = resolve => require(['../page/login/login'], resolve)
const Home = resolve => require(['../page/home/home'], resolve)
const Recommand = resolve => require(['../page/dzkg/recommand'], resolve)
const Search = resolve => require(['../page/dzkg/search'], resolve)
const QA = resolve => require(['../page/dzkg/qa'], resolve)
const UpdatePWD = resolve => require(['../page/set/updatePWD'], resolve)

Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '',
      redirect: '/login'
    },
    {
      path:'/home',
      component: Home,
      meta: { title: '自述文件' },
      children: [
        { //智能推荐
          path: '/recommand',
          component: Recommand,
          meta: { title: '智能推荐' }
        },
        { //搜索
          path: '/search',
          component: Search,
          meta: { title: '搜索' }
        },
        { //搜索
          path: '/qa',
          component: QA,
          meta: { title: '问答' }
        },
        //修改密码
        {
          path: '/UpdatePWD',
          component: UpdatePWD,
          meta: { title: '修改密码'}
        }
      ]
    },
    {
      path: '/login',
      component: Login,
      meta: { title: '登陆' }
    }
  ]


})



