import {
  COLLAPSE,
  DIALOG_SHOW_GRAPH
}from './mutation-type'

export default {
  getCollapse({commit}){
    commit('COLLAPSE');
  },
  dialogshowGraph({commit}){//报表通用弹出层
    commit('DIALOG_SHOW_GRAPH')
  },
}



