//分离vuex方便维护
// # 我们组装模块并导出 store 的地方
import Vue from 'vue'
import Vuex from 'vuex'
import actions from './modules/actions'
import mutations from './modules/mutations'


Vue.use(Vuex);


const state = {
  collapse:false,//折叠面板
  dialogshowGraph: false,//报表通用弹出框
  graph_data: {},
}

export default new Vuex.Store({
  state,
  actions,
  mutations
})



